.Header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 68px;

}

.NavHeader {
    display: flex;
    justify-content: space-between;
    width: 309px;
    height: 34px;
}


.Header img {
    width: 210.32px;
    height: 68px;
}


.NavHeader li {
    font-size: 24px;
    font-weight: 500;
    line-height: 34px;
    letter-spacing: 0em;
    text-align: right;
    color: #FF6060;
    cursor: pointer;
}